<template>
  <v-container>
    <v-card color="basil">
      <v-tabs
        background-color="transparent"
        color="basil"
        grow
      >
        <v-tab
          v-for="(tab,i) in tabs"
          :key="i"
        >
          {{ tab }}
        </v-tab>
        <v-tab-item>
          <base-material-card
            class="px-5 py-3 "
            color="indigo"
            icon="mdi-vuetify"
            inline
          >
            <template v-slot:after-heading>
              <div class="display-2 font-weight-light">
                {{ $t('销量实时统计') }}
              </div>
            </template>
            <v-divider class="mt-3"/>
            <v-data-table
              :headers="headers"
              :items="resData"
              group-by="timeDuration"
              :items-per-page="100"
              group-desc
            >
              <template v-slot:group.header="items">
                <td colspan="100%">
                  <div> {{ $t('time') }} :{{ items.group }} {{ $t('hour') }}</div>
                </td>
              </template>
              <template v-slot:footer>
                <v-toolbar
                  color="white"
                  flat
                >
                  <v-spacer/>
                  <v-btn
                    class="mb-2"
                    color="primary"
                    dark
                    @click="print"
                  >
                    {{ $t('打印') }}
                  </v-btn>
                </v-toolbar>
              </template>
            </v-data-table>
          </base-material-card>
        </v-tab-item>
        <v-tab-item>
          <base-material-card
            class="px-5 py-3 "
            color="indigo"
            icon="mdi-vuetify"
            inline
          >
            <template v-slot:after-heading>
              <div class="display-2 font-weight-light">
                {{ $t('配料实时统计') }}
              </div>
            </template>
            <v-divider class="mt-3"/>
            <v-data-table
              :headers="attrHeaders"
              :items="resAttrData"
              group-by="timeDuration"
              :items-per-page="100"
              group-desc
            >
              <template v-slot:group.header="props">
                <td colspan="100%">
                  <div> {{ $t('time') }} :{{ props.group }} {{ $t('hour') }}</div>
                </td>
              </template>
              <template v-slot:footer>
                <v-toolbar
                  color="white"
                  flat
                >
                  <v-spacer/>
                  <v-btn
                    class="mb-2"
                    color="primary"
                    dark
                    @click="printAttribute"
                  >
                    {{ $t('打印') }}
                  </v-btn>
                </v-toolbar>
              </template>
            </v-data-table>
          </base-material-card>
        </v-tab-item>
      </v-tabs>
    </v-card>
  </v-container>
</template>

<script>
  import { getIndexData, PrinterRealTimeSaleBon, PrinterRealTimeAttributeSaleBon } from '../../components/Utlis/api'
  import Utils from '../../components/Utlis/Utils'

  export default {
    data: function () {
      return ({
        dauer: '',
        tabs: [
          this.$i18n.t('销量实时统计'),
          this.$i18n.t('配料实时统计')],
        headers: [
          {
            text: this.$i18n.t('name'),
            align: 'start',
            value: 'name',
            groupable: false,
          },
          { text: this.$i18n.t('count'), value: 'count', align: 'center', groupable: false },
          { text: this.$i18n.t('sumPrice'), value: 'sumPrice', align: 'center', groupable: false },
          { text: this.$i18n.t('timeDuration'), value: 'timeDuration', align: 'center' },
        ],
        resData: [],
        attrHeaders: [
          {
            text: this.$i18n.t('name'),
            align: 'start',
            value: 'aName',
            groupable: false,
          },
          { text: this.$i18n.t('price'), value: 'singlePriceMod', align: 'center', groupable: false },
          { text: this.$i18n.t('count'), value: 'totalCount', align: 'center', groupable: false },
          { text: this.$i18n.t('sumPrice'), value: 'totalPriceMod', align: 'center', groupable: false },
          { text: this.$i18n.t('timeDuration'), value: 'timeDuration', align: 'center' },
        ],
        resAttrData: [],
      })
    },
    mounted () {
      this.reloadData()
    },
    methods: {
      reloadData () {
        getIndexData().then(res => {
          this.resData = res.everyDurationDishesIncome.filter(item => item.count > 0)
          this.resAttrData = res.attributeIncomeByDuration
        })
      },
      print () {
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie Umsatz Bon drucken?', () => {
          PrinterRealTimeSaleBon().then(() => {
            Utils.toast('Erfolgreich drucken!')
          })
        })
      },
      printAttribute () {
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie Umsatz Bon drucken?', () => {
          PrinterRealTimeAttributeSaleBon().then(() => {
            Utils.toast('Erfolgreich drucken!')
          })
        })
      },
    },
  }
</script>
